.LeftSide {
  grid-area: leftSide;
  background-color: #5e5a5a;
  width: 290px;
  height: 1755px;
}
.foto {
  width: 190px;
  height: 190px;
  border-radius: 50%;
  position: relative;
  left: 44px;
  top: 10px;
  border: 5px white solid;
  object-fit: cover;
}
.contacts {
  margin-top: 10px;
  margin-left: 20px;
  padding-bottom: 10px;
}
.contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 40px;
}
.icon {
  width: 22px;
  height: 22px;
  margin-right: 20px;
  background-color: #5e5a5a;
}

.contact_text {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: white;
  text-decoration: none;
}
.portfolio {
  align-items: center;
  margin: 16px 0 25px 20px;
}
.contact_portfolio {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  color: white;
  text-decoration: none;
  font-size: 14px;
}

.line_contacts {
  width: 270px;
  border-bottom: white 1px solid;
  margin: 10px;
}
.skills_title {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  color: white;
  margin-left: 20px;
  font-size: 30px;
  margin-bottom: 15px;
  margin-top: 20px;
}
.skills {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: white;
  font-size: 17px;
  margin-bottom: 25px;
  list-style: none;
  margin-left: -20px;
}
.skill {
  margin: 5px;
}
.text_p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: white;
  margin-left: 20px;
  font-size: 16px;
}
.about {
  font-family: "Roboto", sans-serif;
  color: white;
  width: 253px;
  font-size: 16px;
  margin-right: 15px;
  margin-left: 20px;
  margin-top: 0;
  text-indent: 0.8em;
}
.languages {
  margin-bottom: 25px;
}
